import Vue from 'vue'
import VueRouter from 'vue-router'
import YiDa1 from '../views/YiDa/YiDa1.vue'
import YiDa2 from '../views/YiDa/YiDa2.vue'
import CRM1 from '../views/CRM/CRM1.vue'
import PersonnelMatters1 from '../views/PersonnelMatters/PersonnelMatters1.vue'
import PersonnelMatters2 from '../views/PersonnelMatters/PersonnelMatters2.vue'
import ProfessionalEdition1 from '../views/ProfessionalEdition/ProfessionalEdition1.vue'
import ProfessionalEdition2 from '../views/ProfessionalEdition/ProfessionalEdition2.vue'
import HomeView from '../views/Home.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomeView',
    component: HomeView,
    meta: { title: '主页' }
  },
  {
    path: '/YiDa1',
    name: 'YiDa1',
    component: YiDa1,
    meta: { title: '宜搭' }
  },
  {
    path: '/YiDa2',
    name: 'YiDa2',
    component: YiDa2,
    meta: { title: '宜搭' }
  },
  {
    path: '/crm1',
    name: 'CRM1',
    component: CRM1,
    meta: { title: 'CRM' }
  },
  {
    path: '/personnel1',
    name: 'PersonnelMatters1',
    component: PersonnelMatters1,
    meta: { title: '人事一体化' }
  },
  {
    path: '/personnel2',
    name: 'PersonnelMatters2',
    component: PersonnelMatters2,
    meta: { title: '人事一体化' }
  },
  {
    path: '/professional1',
    name: 'ProfessionalEdition1',
    component: ProfessionalEdition1,
    meta: { title: '专业版' }
  },
  {
    path: '/professional2',
    name: 'ProfessionalEdition2',
    component: ProfessionalEdition2,
    meta: { title: '专业版' }
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})
router.afterEach((to) => {
  document.title = to.meta.title || '默认标题'
})

export default router
